import { Injectable } from '@angular/core';

import { 
  HttpClient,
  HttpParams 
} from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable()
export class ContactFormService {

  contactForm = 'AKfycbx4o8NDNITD8EdP71m1PMvfyuL596Cdr72fFVz7LsrCk5XVNG8';

  constructor(
    private http: HttpClient
  ) { }

  submitForm(params: { [key: string]: any }): Observable<Boolean> {
    
    let url = `https://script.google.com/macros/s/${this.contactForm}/exec`;
    url += '?' + new HttpParams({ fromObject: params }).toString();
    
    return this.http.jsonp(url, 'callback').pipe(
      catchError(this.handleError('submitForm')),
      map(result => result['result'] === 'success'),
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error);
      return of(result as T);
    };
  }
}



