import { 
  Component, 
  ElementRef,
  OnInit,
  QueryList,
  ViewChildren
} from '@angular/core';

import { ContactFormService } from '../contactform.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.sass']
})
export class ContactComponent implements OnInit {

  @ViewChildren('contact')
  contactFields: QueryList<ElementRef> = null;

  @ViewChildren('submit')
  submitButtons: QueryList<ElementRef> = null;

  contactSubmitSuccess: Boolean = false;

  constructor(
    private service: ContactFormService
  ) { }

  ngOnInit() {
  }

  clearContactForm(): void {
    this.contactFields.forEach(eRef => {
      let e: HTMLElement = <HTMLElement>eRef.nativeElement;
      if (e instanceof HTMLInputElement) {
        (<HTMLInputElement>e).value = '';
      } else if (e instanceof HTMLTextAreaElement) {
        (<HTMLTextAreaElement>e).value = '';
      }
    });
  }

  submitContactForm(event: Event): void {

    if (!this.validateContactForm()) {
      return;
    }

    let fields = {};
    this.contactFields.forEach(eRef => {
      let e: HTMLElement = <HTMLElement>eRef.nativeElement;
      let key = e.attributes.getNamedItem('name').value;
      let value: String = null;
      if (e instanceof HTMLInputElement) {
        value = (<HTMLInputElement>e).value;
      } else if (e instanceof HTMLTextAreaElement) {
        value = (<HTMLTextAreaElement>e).value;
      }
      fields[key] = value;
    });

    this.submitButtons.forEach(eRef => {
      let e: HTMLElement = <HTMLElement>eRef.nativeElement;
      if (!e.classList.contains('is-loading')) {
        e.classList.add('is-loading');
      }
    });

    this.service.submitForm(fields).subscribe(result => {
      if (result) {
        this.clearContactForm();
        this.contactSubmitSuccess = true;
      } else {
        // ?
      }
    }).add(_ => {
      this.submitButtons.forEach(eRef => {
        let e: HTMLElement = <HTMLElement>eRef.nativeElement;
        if (e.classList.contains('is-loading')) {
          e.classList.remove('is-loading');
        }
      });
    });
  }

  validateContactForm(): Boolean { 
    
    let hasErrors: Boolean = false;
    this.contactFields.forEach(eRef => {
      let e: HTMLElement = <HTMLElement>eRef.nativeElement;
      let value: String = null;
      if (e instanceof HTMLInputElement) {
        value = (<HTMLInputElement>e).value;
      } else if (e instanceof HTMLTextAreaElement) {
        value = (<HTMLTextAreaElement>e).value;
      }
      if (value == null || value.length == 0) {
        if (!e.classList.contains('is-danger')) {
          e.classList.add('is-danger');
        }
        hasErrors = true;
      } else {
        if (e.classList.contains('is-danger')) {
          e.classList.remove('is-danger');
        }
      }
    });
    
    return !hasErrors;
  }
}



